<template>
    <v-card>
        <v-card-title> Relatório de Ordens </v-card-title>
        <v-card-text>
            <v-col>
                <v-row>
                    <v-col cols="6" md="3">
                        <v-select
                            v-model="filter.type"
                            :clearable="true"
                            :items="filter.types"
                            label="Tipo"
                        ></v-select>
                    </v-col>
                    <v-col cols="6" md="2">
                        <v-select
                            v-model="filter.billingStatus"
                            :clearable="true"
                            :items="filter.billingStatusList"
                            label="Sit Faturamento"
                        ></v-select>
                    </v-col>
                    <v-col cols="5" md="3">
                        <v-dialog
                            ref="dialogConclusionIni"
                            v-model="modalConclusionIni"
                            :return-value.sync="filter.dtConclusionIni"
                            persistent
                            width="290px"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="computedConclusionIniFormatted"
                                    label="Data Inicial de Conclusão"
                                    prepend-icon="mdi-calendar"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="filter.dtConclusionIni"
                                locale="pt-br"
                                scrollable
                            >
                                <v-spacer></v-spacer>

                                <v-btn
                                    text
                                    color="warning"
                                    @click="filter.dtConclusionIni = null"
                                >
                                    limpa
                                </v-btn>

                                <v-btn
                                    text
                                    color="primary"
                                    @click="modalConclusionIni = false"
                                >
                                    Cancela
                                </v-btn>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="
                                        $refs.dialogConclusionIni.save(
                                            filter.dtConclusionIni
                                        )
                                    "
                                >
                                    OK
                                </v-btn>
                            </v-date-picker>
                        </v-dialog>
                    </v-col>
                    <v-col cols="5" md="3">
                        <v-dialog
                            ref="dialogConclusionEnd"
                            v-model="modalConclusionEnd"
                            :return-value.sync="filter.dtConclusionEnd"
                            persistent
                            width="290px"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="computedConclusionEndFormatted"
                                    label="Data Final de Conclusão"
                                    prepend-icon="mdi-calendar"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="filter.dtConclusionEnd"
                                locale="pt-br"
                                scrollable
                            >
                                <v-spacer></v-spacer>

                                <v-btn
                                    text
                                    color="warning"
                                    @click="filter.dtConclusionEnd = null"
                                >
                                    limpa
                                </v-btn>

                                <v-btn
                                    text
                                    color="primary"
                                    @click="modalConclusionEnd = false"
                                >
                                    Cancela
                                </v-btn>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="
                                        $refs.dialogConclusionEnd.save(
                                            filter.dtConclusionEnd
                                        )
                                    "
                                >
                                    OK
                                </v-btn>
                            </v-date-picker>
                        </v-dialog>
                    </v-col>

                    <v-col cols="1" md="1">
                        <v-btn @click="onClickFilter()" text
                            ><v-icon>mdi-play-outline</v-icon></v-btn
                        >
                    </v-col>
                </v-row>
            </v-col>
            <v-row>
                <v-col md="3" sm="6" cols="12" v-for="(data, type) in reportStatusList" :key="type">
                    <v-simple-table>
                        <tr>
                            <th colspan="3">
                                {{ type }}
                            </th>
                        </tr>

                        <tr v-for="(value, item) in data" :key="item">
                            <td v-bind:class="{'font-weight-bold': item == 'Total'}">{{ item }}</td>
                            <td align="right">{{ value.qtd }}</td>
                            <td align="right">
                                R$ {{ value.priceTotal | decimal }}
                            </td>
                        </tr>
                    </v-simple-table>
                </v-col>
            </v-row>
            <v-row>
                <v-col>Processos {{ orders.length || 0 }}</v-col>
                <v-col>Total {{ totalPrice | decimal() }}</v-col>
            </v-row>
        </v-card-text>
        <order v-bind:order="item" v-bind:activate.sync="showOrder"></order>
    </v-card>
</template>

<script>
import firebase from "firebase";
// import{query, collection, where, getDocs} from "firebase/firestore";
import moment from "moment";
import Order from "./Order.vue";
import { VMoney } from "v-money";

export default {
    directives: { money: VMoney },
    name: "Customer",
    components: {
        Order,
    },
    data() {
        const today = new Date();
        const firstDayOfMonth = new Date(today.getFullYear(), today.getMonth(), 1);
        const lastDayOfMonth = new Date(today.getFullYear(), today.getMonth() + 1, 0);

        return {
            showOrder: false,
            reportStatusList: {},
            filter: {
                status: [],
                type: "",
                billingStatus: "",
                statusList: ["Aberto", "A. Doc", "Andamento", "Final"],
                billingStatusList: ["Aberto", "Faturado", "Pago"],
                types: ["Constituição", "Transformação", "Extinção", "Cisão"],
                dtConclusionIni: firstDayOfMonth.toISOString().substr(0, 10),
                dtConclusionEnd: lastDayOfMonth.toISOString().substr(0, 10),
            },
            modalConclusionIni: false,
            modalConclusionEnd: false,
            orders: [],
            item: {},
            totalPrice: 0,
            money: {
                decimal: ",",
                thousands: ".",
                prefix: "R$ ",
                suffix: " #",
                precision: 2,
                masked: false /* doesn't work with directive */,
            },

            
        };
    },
    created() {
        this.resetReportStatusList();
    },
    computed: {
        computedConclusionIniFormatted() {
            return this.formatDate(this.filter.dtConclusionIni);
        },
        computedConclusionEndFormatted() {
            return this.formatDate(this.filter.dtConclusionEnd);
        },
    },

    methods: {
        async onClickFilter() {
            await this.loadOrders();
        },

        resetReportStatusList() {
            let statusTree = {
                Constituição: { qtd: 0, priceTotal: 0 },
                Transformação: { qtd: 0, priceTotal: 0 },
                Extinção: { qtd: 0, priceTotal: 0 },
                Cisão: { qtd: 0, priceTotal: 0 },
                Total: { qtd: 0, priceTotal: 0 },
            };
            this.reportStatusList.Aberto = JSON.parse(JSON.stringify(statusTree));
            this.reportStatusList["A. Doc"] = JSON.parse(JSON.stringify(statusTree));
            this.reportStatusList.Andamento = JSON.parse(JSON.stringify(statusTree));
            this.reportStatusList.Final = JSON.parse(JSON.stringify(statusTree));
        },

        handleClick(value) {
            this.item = value;
            this.showOrder = true;
        },
        formatDate(value) {
            if (!value) return "--";

            return moment(value).format("DD/MM/YY");
        },

        addFilters(query) {
            if (this.filter.status.length > 0) {
                query = query.where("status", "in", this.filter.status);
            }
            if (this.filter.type && this.filter.type.length > 0) {
                query = query.where("type", "==", this.filter.type);
            }
            if (
                this.filter.billingStatus &&
                this.filter.billingStatus.length > 0
            ) {
                query = query.where(
                    "billingStatus",
                    "==",
                    this.filter.billingStatus
                );
            }

            if (this.filter.dtConclusionIni && this.filter.dtConclusionEnd) {
                query = query
                    .where("conclusion", ">=", this.filter.dtConclusionIni)
                    .where("conclusion", "<=", this.filter.dtConclusionEnd);
            }
            return query;
        },

        async loadOrders() {
            var query = await firebase.firestore().collection("order");
            query = this.addFilters(query);
            // query = query.orderBy("creation", "desc");
            this.totalPrice = 0;
            this.orders = [];
            

            const snapshot = await query.get();
            this.orders = snapshot.docs.map((doc) => doc.data());

            this.resetReportStatusList();

			const that = this;
            this.orders.forEach((ord) => {
                this.totalPrice += ord.price;

                console.log(`${ord.status} - ${ord.type} - ${ord?.price} `);

                if (!that.filter.statusList.includes(ord.status)) {
                    console.log(ord);
                } else {
                    that.reportStatusList[ord.status][ord.type].priceTotal +=
                        ord?.price;
                    that.reportStatusList[ord.status].Total.priceTotal +=
                        ord?.price;

                    that.reportStatusList[ord.status][ord.type].qtd++;
                    that.reportStatusList[ord.status].Total.qtd++;

                }
				console.log(that.reportStatusList[ord.status][ord.type]);
            });
        },
    },
};
</script>

<style lang="scss" scoped>
$data-table-regular-row-font-size: 8em !default;
</style>>
